    stroke-width: 1px;
    stroke-dasharray: 400;
    animation: 30s write;
  }

  @keyframes write {
      0% {
          stroke-dashoffset: 400;
      }
      100% {
          stroke-dashoffset: 0;
      }
  }
.bg {
 width: 100%;
 height: 100%;
 position: absolute;
 left: 0;
 top:0;
 z-index: 1;
 opacity: .1;
 overflow: hidden;
  svg {
    width: 100%;
    // height: 100%;
  }
}
</style>



